// src/utils/formInputFormatters.js

/**
 *     (   ).
 *      .
 * @param {string} value -   
 * @returns {string} -    ( )
 */
export const formatPhoneNumber = (value) => {
  if (!value) return '';
  const cleaned = value.replace(/\D/g, ''); //  
  const len = cleaned.length;

  if (len < 4) {
    return cleaned;
  } else if (len < 7) {
    return cleaned.slice(0, 3) + '-' + cleaned.slice(3);
  } else if (len < 11) {
    // 010-1234-5678 (11)
    return cleaned.slice(0, 3) + '-' + cleaned.slice(3, 7) + '-' + cleaned.slice(7);
  } else {
    // 010-1234-56789 (12  ,  )
    return cleaned.slice(0, len - 8) + '-' + cleaned.slice(len - 8, len - 4) + '-' + cleaned.slice(len - 4);
  }
};

/**
 *      .
 *      .
 * @param {string} value -   
 * @returns {string} -    ( )
 */
export const formatTelephoneNumber = (value) => {
  if (!value) return '';
  let raw = value.replace(/\D/g, ''); //  

  let isSeoul = raw.startsWith('02');
  let formatted = raw;

  if (isSeoul) {
    if (raw.length <= 2) {
      formatted = raw;
    } else if (raw.length <= 6) {
      formatted = raw.slice(0, 2) + '-' + raw.slice(2);
    } else if (raw.length <= 10) { // 02-xxxx-xxxx (10)
      formatted = raw.slice(0, 2) + '-' + raw.slice(2, 6) + '-' + raw.slice(6);
    } else { // 02-xxx-xxxx-xxxx (11 ,  )
      formatted = raw.slice(0, 2) + '-' + raw.slice(2, raw.length - 4) + '-' + raw.slice(raw.length - 4);
    }
  } else { // 0X0, 0XX (3 )
    if (raw.length <= 3) {
      formatted = raw;
    } else if (raw.length <= 7) {
      formatted = raw.slice(0, 3) + '-' + raw.slice(3);
    } else { // 0X0-xxxx-xxxx (11)  0XX-xxxx-xxxx (11)
      formatted = raw.slice(0, 3) + '-' + raw.slice(3, 7) + '-' + raw.slice(7, 11);
    }
  }
  return formatted;
};

/**
 *   .
 *  9~15 //(~, !, @, #, $, *, (, ), =, +, _, ., |)  .
 * @param {string} password -  
 * @returns {object} - { isValid: boolean, message: string }
 */
export const validatePassword = (password) => {
  const specialChars = "~!@#$*()=+_.|";
  //     .
  const escapedSpecialChars = specialChars.replace(/[-[\]/{}()*+?.\\^$|]/g, '\\$&');

  // (``)     .
  const regex = new RegExp(
    `^(?=.*[A-Za-z])(?=.*\\d)(?=.*[${escapedSpecialChars}])[A-Za-z\\d${escapedSpecialChars}]{9,15}$`
  );

  if (password.length === 0) {
    return { isValid: false, message: "" }; //    
  }

  if (!regex.test(password)) {
    return { isValid: false, message: "   ." };
  } else {
    return { isValid: true, message: "  ." };
  }
};