// src/utils/formatters.js

/**
 * ISO 8601  /  "YY-MM-DD / HH:MM"  .
 * : "2025-05-07T00:57:31.000+00:00" -> "25-05-07 12:57"
 * @param {string} isoString - ISO 8601  / 
 * @returns {string}  /    
 */
export const formatDateTime = (isoString) => {
  if (!isoString) {
    return '';
  }

  const date = new Date(isoString);

  // Date       
  if (isNaN(date.getTime())) {
    console.error("Invalid date string provided to formatDateTime:", isoString);
    return '';
  }

  const formatter = new Intl.DateTimeFormat('ko-KR', {
    year: '2-digit',
    month: '2-digit',
    day: '2-digit',
    hour: '2-digit',
    minute: '2-digit',
    hour12: true,
    // timeZone: 'Asia/Seoul', //    ( UTC   )
  });

  const formattedDate = formatter.format(date);

  //    
  return formattedDate
    .replace(/\.\s/g, '-') // ". " "-"  (: 25. 05. 07. -> 25-05-07)
    .replace(/\.$/, '')   //    (: 25-05-07.)
    .replace(' ', '') // ' ' -> ''
    .replace(' ', ''); // ' ' -> ''
};