// services/faqService.js
import axios from 'axios';

const API_BASE_URL = 'http://localhost:8485/api'; // Spring Boot API   

export const fetchFaqs = async (params) => {
  try {
    // : /api/faqs?pageNum=1&amount=10&type=T&keyword=
    const response = await axios.get(`${API_BASE_URL}/faqs`, { params });
    return response.data; //    (list pageMaker  )
  } catch (error) {
    console.error('FAQ    :', error);
    throw error;
  }
};