// src/components/FormEditor.js
import React, { useState, useEffect } from 'react';
import './FormEditor.css'; //    CSS

function FormEditor({ fields, onSubmit, initialData = {}, submitButtonText = "", showTimeField = true }) {
  // initialData    
  const [formData, setFormData] = useState(initialData);
  const [loading, setLoading] = useState(false); 
  const [error, setError] = useState(null);
  const [successMessage, setSuccessMessage] = useState(null); 
  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prevData => ({
      ...prevData,
      [name]: value,
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    setError(null);
    setSuccessMessage(null);

    try {
      //    onSubmit  
      await onSubmit(formData);
      setSuccessMessage(' !');
      //     ( )
      // setFormData(initialData); //      
    } catch (err) {
      //     (     )
      if (err.response && err.response.data && err.response.data.message) {
        setError(err.response.data.message);
      } else if (err.message) {
        setError(' : ' + err.message);
      } else {
        setError('    .');
      }
    } finally {
      setLoading(false);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="faqann-form" data-aos="fade" data-aos-delay="100">
      <div className="row gy-4">

        {/*  ID  (   ) */}
        <div className="col-md-6">
          <input type="text" className="form-control" placeholder=" ID" value="" readOnly />
        </div>

        {/*    (   ) */}
        {showTimeField && (
          <div className="col-md-6">
            <input type="text" className="form-control" value={new Date().toLocaleString()} readOnly />
          </div>
        )}

        {/*    */}
        {fields.map((field) => (
          <div className="col-md-12" key={field.name}>
            {field.type === 'text' && (
              <input
                type="text"
                className="form-control"
                name={field.name}
                placeholder={field.placeholder}
                value={formData[field.name] || ''}
                onChange={handleChange}
                required={field.required}
              />
            )}
            {field.type === 'textarea' && (
              <textarea
                className="form-control"
                name={field.name}
                rows={field.rows || 8}
                placeholder={field.placeholder}
                value={formData[field.name] || ''}
                onChange={handleChange}
                required={field.required}
              ></textarea>
            )}
            {/*    (number, select )   */}
          </div>
        ))}

        <div className="col-md-12 text-center">
          {loading && <div className="loading"> ...</div>}
          {error && <div className="error-message" style={{ color: 'red', marginTop: '10px' }}>{error}</div>}
          {successMessage && <div className="sent-message" style={{ color: 'green', marginTop: '10px' }}>{successMessage}</div>}

          <button type="submit" disabled={loading} style={{
            padding: '10px 20px',
            backgroundColor: '#007bff',
            color: 'white',
            border: 'none',
            borderRadius: '5px',
            cursor: loading ? 'not-allowed' : 'pointer',
            marginTop: '20px'
          }}>
            {loading ? ' ...' : submitButtonText}
          </button>
        </div>
      </div>
    </form>
  );
}

export default FormEditor;