// services/api.js
import axios from 'axios';

const API_BASE_URL = 'http://localhost:8485/api'; //   URL

export const fetchTestData = async () => {
  try {
    const response = await axios.get(`${API_BASE_URL}/test`);
    return response.data;
  } catch (error) {
    console.error('   :', error);
    return null;
  }
};