// pages/FaqPage.js
import React, { useEffect, useState, useCallback } from 'react';
import { useSearchParams, Link } from 'react-router-dom';
import FaqItem from '../components/FaqItem'; // FaqItem  import
import AnnounceSearch from '../components/AnnounceSearch'; // AnnouncePage 
import Pagination from '../components/Pagination'; // AnnouncePage 
import { fetchFaqs } from '../services/faqService'; // FAQ  import
import '../assets/Global.css'; //   

function FaqPage() {
  const [faqs, setFaqs] = useState([]);
  const [pageMaker, setPageMaker] = useState({
    cri: { pageNum: 1, amount: 10, type: '', keyword: '' },
    total: 0,
    startPage: 1,
    endPage: 1,
    prev: false,
    next: false,
  });
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  const [searchParams, setSearchParams] = useSearchParams();

  useEffect(() => {
    const initialPageNum = parseInt(searchParams.get('pageNum')) || 1;
    const initialAmount = parseInt(searchParams.get('amount')) || 10;
    const initialType = searchParams.get('type') || '';
    const initialKeyword = searchParams.get('keyword') || '';

    //  cri   loadFaqs .
    // pageMaker.cri    ,   cri   .
    loadFaqs({
      pageNum: initialPageNum,
      amount: initialAmount,
      type: initialType,
      keyword: initialKeyword,
    });
  }, []); //  :     

  //   . useCallback    .
  const loadFaqs = useCallback(async (params) => {
    setLoading(true);
    setError(null);
    try {
      const data = await fetchFaqs(params);
      setFaqs(data.list);
      setPageMaker(data.pageMaker);

      // URL      !
      setSearchParams(new URLSearchParams(params).toString());

    } catch (err) {
      setError('FAQ   .');
      console.error(err);
    } finally {
      setLoading(false);
    }
  }, [setSearchParams]); // loadFaqs  setSearchParams!

  //   :  cri     .
  const handlePageChange = useCallback((newCri) => {
    // pageMaker.cri   ,     .
    setPageMaker(prev => ({ ...prev, cri: newCri }));
    loadFaqs(newCri);
  }, [loadFaqs]);

  //  :       .
  const handleSearch = useCallback((searchParams) => {
    //    pageNum 1 .
    const updatedCri = { ...pageMaker.cri, ...searchParams, pageNum: 1 };
    setPageMaker(prev => ({ ...prev, cri: updatedCri })); //  
    loadFaqs(updatedCri); //   
  }, [pageMaker.cri, loadFaqs]); // pageMaker.cri      


  if (loading) return <div style={{ textAlign: 'center', padding: '20px' }}>FAQ  ...</div>;
  if (error) return <div style={{ textAlign: 'center', padding: '20px', color: 'red' }}>: {error}</div>;

  return (
    <main id="main">
      {/* Page Title */}
      <div className="page-title">
        <div className="heading">
          <div className="container">
            <div className="row d-flex justify-content-center text-center">
              <div className="col-lg-8">
                <h1>FAQ</h1>
                <p className="mb-0">    FAQ .</p>
              </div>
            </div>
          </div>
        </div>
        <nav className="breadcrumbs">
          <div className="container">
            <ol>
              <li><Link to="/"></Link></li>
              <li className="current">FAQ</li>
            </ol>
          </div>
        </nav>
      </div>{/* End Page Title */}

      {/* Starter Section Section */}
      <section id="starter-section" className="starter-section section">
        <div className="container section-title" data-aos="fade-up" style={{ listStyleType: 'none' }}>
          {/* Faq Section */}
          <section id="faq" className="faq section">
            <div className="container">
              <div className="row gy-4">
                <div className="col-lg-4" data-aos="fade-up" data-aos-delay="100">
                  <div className="content px-xl-5">
                    <h3><span>  </span><strong></strong></h3>
                  </div>
                </div>

                <div className="col-lg-8" data-aos="fade-up" data-aos-delay="200">
                  <div className="faq-container">
                    {faqs.length > 0 ? (
                      faqs.map((item) => (
                        <FaqItem key={item.id} item={item} /> // FaqItem  
                      ))
                    ) : (
                      <p style={{ textAlign: 'center' }}> FAQ .</p>
                    )}
                  </div>
                </div>
              </div>
            </div>
          </section>{/* /Faq Section */}

          {/* Blog Pagination Section (AnnouncePage ) */}
          <section id="blog-pagination" className="blog-pagination section">
            <AnnounceSearch
              onSearch={handleSearch}
              initialType={pageMaker.cri.type}
              initialKeyword={pageMaker.cri.keyword}
            />
            <Pagination pageMaker={pageMaker} onPageChange={handlePageChange} />
          </section>{/* /Blog Pagination Section */}

          <div style={{ textAlign: 'right', marginTop: '20px' }}>
             <Link to="/notice_write" style={{ padding: '10px 20px', backgroundColor: '#007bff', color: 'white', border: 'none', borderRadius: '5px', textDecoration: 'none' }}></Link>
          </div>
        </div>
      </section>{/* /Starter Section Section */}
    </main>
  );
}

export default FaqPage;