// layout/Header.js
import React from 'react';
import { Link } from 'react-router-dom';
import MenuItem from '../components/MenuItem'; // MenuItem  import

//     (  )
const menuItems = [
  { label: '', link: '/recall_list' },
  {
    label: '',
    link: '#', //     # ,    
    children: [
      { label: '', link: '/defect_reports' },
      { label: '', link: '/defectList' },
    ],
  },
  {
    label: '',
    link: '#',
    children: [
      { label: '', link: '/announce' },
      { label: 'FAQ', link: '/notice' },
    ],
  },
  { label: '', link: '/recall_statics_year' },
  {
    label: '',
    link: '#',
    children: [
      { label: '', link: '/defect_details_check' },
      { label: '', link: '/announce_write' },
    ],
  },
];

function Header() {
  return (
    <header style={{ backgroundColor: '#333', color: 'white', padding: '10px', display: 'flex', alignItems: 'center' }}>
      <Link to="/" style={{ textDecoration: 'none', color: 'inherit' }}>
        <h1 style={{ margin: 0 }}>Recall center</h1>
      </Link>

      <nav id="navmenu" className="navmenu" style={{ marginLeft: 'auto' }}>
        <ul>
          {menuItems.map((item, index) => (
            <MenuItem key={index} item={item} /> // MenuItem  
          ))}
        </ul>
        <i className="mobile-nav-toggle d-xl-none bi bi-list"></i>
      </nav>
    </header>
  );
}

export default Header;