// src/pages/AnnounceWritePage.js
import React from 'react';
import { useNavigate, Link } from 'react-router-dom';
import axios from 'axios';
import FormEditor from '../components/FormEditor'; // FormEditor import

function AnnounceWritePage() {
  const navigate = useNavigate();
  const initialAnnounceData = { title: '', content: '' }; 
  // FormEditor   
  const fields = [
    { name: 'title', type: 'text', placeholder: '', required: true },
    { name: 'content', type: 'textarea', placeholder: '', required: true, rows: 8 },
  ];

  //   
  const handleAnnounceSubmit = async (formData) => {
    const API_URL = 'http://localhost:8485/api/announce/write'; //   API 

    //  AnnounceDTO   formData   
    // : const payload = { ...formData, writer: '' };

    await axios.post(API_URL, formData); //  

    //     
    setTimeout(() => {
      navigate('/announce'); //    
    }, 1500);
  };

  return (
    <main id="main">
      {/* Page Title */}
      <div className="page-title">
        <div className="heading">
          <div className="container">
            <div className="row d-flex justify-content-center text-center">
              <div className="col-lg-8">
                <h1> </h1>
                <p className="mb-0">  .</p>
              </div>
            </div>
          </div>
        </div>
        <nav className="breadcrumbs">
          <div className="container">
            <ol>
              <li><Link to="/"></Link></li>
              <li><Link to="/announce"></Link></li>
              <li className="current"> </li>
            </ol>
          </div>
        </nav>
      </div>{/* End Page Title */}

      <section id="announce-write-section" className="announce-write-section section" style={{ padding: '40px 0' }}>
        <div className="container" data-aos="fade-up">
          <div className="row justify-content-center">
            <div className="col-lg-8">
              <FormEditor
                  fields={fields}
                  onSubmit={handleAnnounceSubmit}
                  initialData={initialAnnounceData}
                  submitButtonText=" "
              />
               <div className="col-md-12 text-center" style={{ marginTop: '20px' }}>
                    <Link to="/announce" style={{
                        padding: '10px 20px',
                        backgroundColor: '#6c757d',
                        color: 'white',
                        border: 'none',
                        borderRadius: '5px',
                        textDecoration: 'none',
                        display: 'inline-block',
                        verticalAlign: 'middle',
                        cursor: 'pointer'
                    }}></Link>
                </div>
            </div>
          </div>
        </div>
      </section>
    </main>
  );
}

export default AnnounceWritePage;