// src/pages/FaqWritePage.js ()
import React from 'react';
import { useNavigate, Link } from 'react-router-dom';
import axios from 'axios';
import FormEditor from '../components/FormEditor'; // FormEditor import

function FaqWritePage() {
  const navigate = useNavigate();
  const initialFaqData = { question: '', answer: '' };
  // FormEditor   
  const fields = [
    { name: 'question', type: 'text', placeholder: '', required: true },
    { name: 'answer', type: 'textarea', placeholder: '', required: true, rows: 8 },
  ];

  // FAQ  
  const handleFaqSubmit = async (formData) => {
    const API_URL = 'http://localhost:8485/api/faqs/write'; // FAQ  API 

    //  FaqsDTO   formData   
    // : const payload = { ...formData, writer: '' };

    await axios.post(API_URL, formData); //  

    //   FAQ  
    setTimeout(() => {
      navigate('/notice'); // FAQ   
    }, 1500);
  };

  return (
    <main id="main">
      {/* Page Title (AnnounceWritePage  ) */}
      <div className="page-title">
        <div className="heading">
          <div className="container">
            <div className="row d-flex justify-content-center text-center">
              <div className="col-lg-8">
                <h1>FAQ </h1>
                <p className="mb-0">    .</p>
              </div>
            </div>
          </div>
        </div>
        <nav className="breadcrumbs">
          <div className="container">
            <ol>
              <li><Link to="/"></Link></li>
              <li><Link to="/notice">FAQ</Link></li>
              <li className="current">FAQ </li>
            </ol>
          </div>
        </nav>
      </div>{/* End Page Title */}

      <section id="faq-write-section" className="faq-write-section section" style={{ padding: '40px 0' }}>
        <div className="container" data-aos="fade-up">
          <div className="row justify-content-center">
            <div className="col-lg-8">
              <FormEditor
                  fields={fields}
                  onSubmit={handleFaqSubmit}
                  initialData={initialFaqData} //    
                  submitButtonText="FAQ "
              />
               <div className="col-md-12 text-center" style={{ marginTop: '20px' }}>
                    <Link to="/notice" style={{
                        padding: '10px 20px',
                        backgroundColor: '#6c757d',
                        color: 'white',
                        border: 'none',
                        borderRadius: '5px',
                        textDecoration: 'none',
                        display: 'inline-block',
                        verticalAlign: 'middle',
                        cursor: 'pointer'
                    }}></Link>
                </div>
            </div>
          </div>
        </div>
      </section>
    </main>
  );
}

export default FaqWritePage;